











import { Component, Vue, Prop } from "vue-property-decorator";

@Component
export default class ConfirmationModal extends Vue {
    @Prop()
    cancel: () => any;

    @Prop()
    confirm: () => any;

    @Prop({ default: true })
    hasCancel: boolean;

    @Prop({ default: 'Cancel' })
    confirmText: string;

    @Prop({ default: 'Confirm' })
    cancelText: string;

    @Prop({ default: 'neutral' })
    confirmStyle: string;
}
